.box {
  min-height: 300px;
  box-shadow: 0px 0px 3px 1px #bdc3c7;
  background: white;
  border-radius: 5px;
  padding: 10px;
}
.Message {
  min-height: 300px;
  box-shadow: 0px 0px 3px 1px #bdc3c7;
  background: white;
  border-radius: 5px;
  padding: 10px;
  display: flex;
  flex-direction: column;
}
.Message .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Message .title .right {
  color: #3498db;
}
.Message .fgx {
  margin: 10px 0;
}
.Message .Contents {
  flex: 1;
  min-height: 200px;
  overflow: auto;
  background-color: #F4F4F4;
  padding: 5px;
}
